import { Cliente } from './cliente.model';

export class Formulario {
    imagem: string;
    titulo: string;
    empreendimento: string;
    unidade: string;
    dadosClientes: Cliente[];
    valorImovel: number;
    formaPagamento: string;

    constructor() {
        this.titulo = 'PROPOSTA DE COMPRA DO IMOVEL';
        this.dadosClientes = new Array();
        this.dadosClientes.push(new Cliente());
        this.imagem = 'data:image/png;base64, ';
    }
}
